<template>
  <img alt="Vue logo" src="./assets/j.jpg">
  <h1>{{ firstname }} {{ lastname }}</h1>
  <button v-on:click="showalert1">แสดงชื่อ</button>
  <button @:click="showalert2">แสดงนามสกุล</button>
  <h1>{{age}}</h1>
  <button @:click="plus">เพิ่มตัวเลข</button>
  <button @:click="minus">ลดตัวเลข</button>
  <button @:click="plus5">เพิ่มตัวเลขที่ละ5</button>
  <button @:click="minus5">ลดตัวเลขที่ละ5</button>
  <form action="">
    <label for="">กรุณากรอกชื่อจริง</label>
    <input type="text" v-on:input="fname"><br>
    <label for="">กรุณากรอกนามสกุล</label>
    <input type="text" v-on:input="lname"><br>
    <label for="">กรุณากรอกชื่อเล่น</label>
    <input type="text" v-on:input="nickname"><br>
    <label for="">กรุณากรอกอายุ</label>
    <input type="text" v-on:input="setage"><br>
    <label for="">กรุณากรอกวัน/เดือน/ปีเกิด</label>
    <input type="text" v-on:input="date"><br>
    <label for="">กรุณากรอกสาขา</label>
    <input type="text" v-on:input="major"><br>
    <label for="">กรุณากรอกห้อง</label>
    <input type="text" v-on:input="room"><br>
    <label for="">กรุณากรอกคติปรจำใจ</label>
    <input type="text" v-on:input="motto"><br>
    <label for="">กรุณากรอกสถานที่เรียนต่อ</label>
    <input type="text" v-on:input="sh"><br>
    <label for="">กรุณากรอกวิชาที่ไม่ชอบ</label>
    <input type="text" v-on:input="sub"><br>
  </form>
  <h1>{{"แสดงชื่อจริง :"+showfname}}</h1>
  <h1>{{"แสดงนามสกุล :"+showlname}}</h1>
  <h1>{{"แสดงชื่อเล่น :"+shownickname}}</h1>
  <h1>{{"แสดงอายุ :"+showsetage}}</h1>
  <h1>{{"แสดงวัน/เดือน/ปีเกิด :"+showdate}}</h1>
  <h1>{{"แสดงสาขา :"+showmajor}}</h1>
  <h1>{{"แสดงห้อง :"+showroom}}</h1>
  <h1>{{"แสดงคติปรจำใจ :"+showmotto}}</h1>
  <h1>{{"แสดงสถานที่เรียนต่อ :"+showsh}}</h1>
  <h1>{{"แสดงวิชาที่ไม่ชอบ :"+showsub}}</h1>
</template>

<script>


export default {
  name: 'App',
  data(){
    return{
      firstname: "panupong ",
      lastname: " paeksuwan",
      age: 19 ,
      showfname : "",
      showlname : "",
      shownickname : "",
      showsetage : "",
      showdate : "",
      showmajor : "",
      showroom : "",
      showmotto : "",
      showsh : "",
      showsub : "",
    }
  },
  methods:{
    showalert1(){
      alert(this.firstname)
    },
    showalert2(){
      alert(this.lastname)
    },
    plus(){
      this.age++
    },
    minus(){
      this.age--
    },
    plus5(){
      this.age+=5
    },
    minus5(){
      this.age-=5
    },
    fname(event){
      this.showfname = event.target.value
    },
    lname(event){
      this.showlname = event.target.value
    },
    nickname(event){
      console.log(event.target.value);
      this.shownickname = event.target.value
    },
    setage(event){
      this.showsetage = event.target.value
    },
    date(event){
      this.showdate = event.target.value
    },
    major(event){
      this.showmajor = event.target.value
    },
    room(event){
      this.showroom = event.target.value
    },
    motto(event){
      this.showmotto = event.target.value
    },
    sh(event){
      this.showsh = event.target.value
    },
    sub(event){
      this.showsub = event.target.value
    },
  }
}
</script>

